.sidebar {
  position: absolute;
  left: -50%;
  z-index: 3;
  top: 0;
  width: 300px;
  background-color: #fff;
  box-shadow: 1px 0 5px #0000007e;
  height: 100%;
  transition: 0.4s;
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.sidebar.open {
  left: 0;
}

.background {
  width: 100%;
  position: absolute;
  height: 100%;
  background-color: #00000077;
  z-index: 2;
  transition: 0.2s;
}

.nav {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.nav a {
  color: #000;
  text-decoration: none;
}

.nav a:hover {
  color: #0059B2;
  text-decoration: underline;
}

.nav a.active {
  color: #0059B2;
  text-decoration: underline;
}
.close {
  align-self: flex-end;
  background-color: #fff;
  border: 0;
  cursor: pointer;
}

