.input:focus {
  outline: none;
}

.input {
  padding: 10px 20px;
  font-size: 16px;
  font-family: "Montserrat";
  border: 1px solid #D1D1D1;
  border-radius: 5px;
  width: 350px;
}

.inputWrapper {
  display: flex;
  flex-direction: column;
}

.superInput {
}

.errorInput {
  border: 1px solid #CC1439;
}

.errorInput:focus {
}

.error {
  order: -1;
  font-size: 12px;
  line-height: 12px;
  color: #CC1439;
  font-weight: 600;
  width: 100%;
  height: 12px;
}
