.button {
  padding: 10px 20px;
  min-width: 100px;
  border: 0;
  font-family: "Montserrat";
  border-radius: 3px;
  font-weight: 600;
  cursor: pointer;
  transition: 0.1s;
}

.button::first-letter {
  text-transform: uppercase;
}

.disabled {
  background-color: #004d997a;
  color: #002e5c86;
  cursor: default;
}

.secondary {
  background-color: #fff;
  border: 2px solid #0066CC;
  color: #0066CC;
}

.default {
  background-color: #0066CC;
  color: #fff;
}

.red {
  background-color: #CC1439;
  color: #fff;
}

.default:hover {
  background-color: #0056ac;
}

.red:hover {
  background-color: #b61233;
}

.secondary:hover {
  background-color: #0066CC;
  color: #fff;
}


