.greetingForm {
}

.inputAndButtonContainer {
  display: flex;
  gap: 20px;
  margin: 0 0 20px 0;
}

.error {
  margin: 10px 0 0 0;
  height: 16px;
  width: 100%;
}

.input {
  padding: 10px 20px;
  width: 350px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #D1D1D1;
  font-family: "Montserrat";
}

.input:focus {
  outline: none;
  border: 1px solid #9e9e9e;
}

.errorInput {
  box-shadow: 0 2px red;
}

.button {
  align-self: flex-start;
  font-size: 16px;
  padding: 10px 20px;
  border-radius: 5px;
  border: 0;
  background-color: #0066CC;
  cursor: pointer;
  color: #fff;
  font-family: "Montserrat";
}

.button:focus {
}

.button:active {
}

.button:disabled {
  background-color: #0064c993
}

.text {
  color: #00000080;
  margin: 0 0 20px 0;
}

